import React, {Component} from 'react';
import RelatedNews from '../../components/LeftSidebar/RelatedNews';
import SearchForm from '../../components/LeftSidebar/SearchForm';
import TagSearchNews from '../../components/LeftSidebar/TagSearchNews';
import ItemNews from '../../components/News/ItemNews';
import Breadcrumb from '../../components/Breadcrumb';
import {connect} from "react-redux";
import * as Actions from "../../actions";
import Loading from "../../components/Loading";
import Pagination from "react-pagination-library";
import "react-pagination-library/build/css/index.css";

import {Helmet} from 'react-helmet';
const loadJs = require('loadjs');

class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentPage: 1,
            params: {
                page: 1,
            }
        };
        this.myRef = React.createRef();
    }

    changeCurrentPage = numPage => {
        console.log(numPage);
        this.setState({
                currentPage: numPage,
                params: {
                    page: numPage
                }
            }, () =>
                this.props.fetchData(this.state.params)
        );
        this.myRef.current.scrollIntoView({behavior: 'smooth'});
        //window.scrollTo(0, this.myRef.current.offsetTop); //cach 2
        //fetch a data
        //or update a query to get data
    };

    showData = () => {
        if (this.props.data != null && this.props.fetching === false) {
            return this.props.data.data.map((item, key) => {
                return (
                    <div key={key} className="col-lg-6 col-md-6 col-sm-6">
                        <ItemNews data={item}/>
                    </div>
                );
            })
        } else {
            return <Loading/>
        }
    }

    componentDidMount() {
        loadJs('/js/main.js');
        this.props.fetchData(this.state.params);
    }

    render() {
        const moduleName = 'Tin tức';
        return (
            <div>
                <Helmet>
                    <title>{moduleName}</title>
                </Helmet>
                <Breadcrumb module={moduleName} title={moduleName}/>
                <section className="blog spad">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-4 col-md-5">
                                <div className="blog__sidebar">
                                    <div className="blog__sidebar__search">
                                        <SearchForm/>
                                    </div>
                                    <div className="blog__sidebar__item">
                                        <RelatedNews data={this.props.dataDefault}/>
                                    </div>
                                    {/*<div className="blog__sidebar__item">*/}
                                    {/*<TagSearchNews/>*/}
                                    {/*</div>*/}
                                </div>
                            </div>
                            <div className="col-lg-8 col-md-7">
                                <div ref={this.myRef} className="row">
                                    {this.showData()}
                                    <Pagination
                                        currentPage={this.state.currentPage}
                                        totalPages={this.props.data ? this.props.data.totalPages : 0}
                                        changeCurrentPage={this.changeCurrentPage}
                                        theme="bottom-border border-bottom"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        fetching: state.payloadNews.fetching,
        data: state.payloadNews.data,
        error: state.payloadNews.error,
        dataDefault: state.payloadDataDefault.data
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchData: (params) => dispatch(Actions.newsAction.requestNewsData(params))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Index);